import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import searchFill from '@iconify/icons-eva/search-fill';
// material
import { useTheme, styled } from '@mui/material/styles';
import {
  Stack,
  Tooltip,
  IconButton,
  Button,
  TextField,
  MenuItem,
  OutlinedInput,
  InputAdornment,
  Box
} from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { DatePicker } from '@mui/x-date-pickers';

// ----------------------------------------------------------------------

const SearchStyle = styled(OutlinedInput)(({ theme }) => ({
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter
  }),
  '&.Mui-focused': { width: 200, boxShadow: theme.customShadows.z8 },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${theme.palette.grey[500_32]} !important`
  }
}));

RapportListToolbar.propTypes = {
  filterName: PropTypes.string,
  onFilterName: PropTypes.func,
  value: PropTypes.string,
  value1: PropTypes.string,
  value2: PropTypes.string,
  value3: PropTypes.string,
  value4: PropTypes.string,
  onChangeDate1: PropTypes.func,
  onChangeDate2: PropTypes.func,
  setValue2: PropTypes.func,
  setValue3: PropTypes.func,
  setValue4: PropTypes.func,
  handleGo: PropTypes.func,
  loading: PropTypes.bool,
  setLoading: PropTypes.func,
  agents: PropTypes.arrayOf(PropTypes.object),
  surcussales: PropTypes.arrayOf(PropTypes.object),
  infoTirages: PropTypes.arrayOf(PropTypes.object)
};

const INPUT_WIDTH = 130;

export default function RapportListToolbar({
  filterName,
  onFilterName,
  onChangeDate1,
  onChangeDate2,
  handleGo,
  value,
  value1,
  value2,
  value3,
  value4,
  setValue2,
  setValue3,
  setValue4,
  loading,
  setLoading,
  agents,
  surcussales,
  infoTirages
}) {
  const role = window.localStorage.getItem('role');

  return (
    <Stack spacing={2} direction={{ xs: 'column', md: 'row' }} sx={{ py: 2.5, px: 3 }}>
      <TextField
        fullWidth
        select
        label="Tirage"
        value={value2}
        onChange={(event) => setValue2(event.target.value)}
        SelectProps={{
          MenuProps: {
            sx: { '& .MuiPaper-root': { maxHeight: 260 } }
          }
        }}
        sx={{
          maxWidth: { md: INPUT_WIDTH },
          textTransform: 'capitalize'
        }}
      >
        {[{ id: 0, name: 'Tout' }, ...infoTirages]?.map((option) => (
          <MenuItem
            key={option?.id}
            value={option?.id}
            sx={{
              mx: 1,
              my: 0.5,
              borderRadius: 0.75,
              typography: 'body2',
              textTransform: 'capitalize'
            }}
          >
            {option?.name}
          </MenuItem>
        ))}
      </TextField>
      {role !== 'supervisor' && (
        <TextField
          fullWidth
          select
          label="Surcussale"
          value={value3}
          onChange={(event) => setValue3(event.target.value)}
          SelectProps={{
            MenuProps: {
              sx: { '& .MuiPaper-root': { maxHeight: 260 } }
            }
          }}
          sx={{
            maxWidth: { md: INPUT_WIDTH },
            textTransform: 'capitalize'
          }}
        >
          {[{ id: 0, addresse_complet: 'Tout' }, ...surcussales]?.map((option) => (
            <MenuItem
              key={option?.id}
              value={option?.id}
              sx={{
                mx: 1,
                my: 0.5,
                borderRadius: 0.75,
                typography: 'body2',
                textTransform: 'capitalize'
              }}
            >
              {option?.addresse_complet}
            </MenuItem>
          ))}
        </TextField>
      )}

      <TextField
        fullWidth
        select
        label="Agent"
        value={value4}
        onChange={(event) => setValue4(event.target.value)}
        SelectProps={{
          MenuProps: {
            sx: { '& .MuiPaper-root': { maxHeight: 260 } }
          }
        }}
        sx={{
          maxWidth: { md: INPUT_WIDTH },
          textTransform: 'capitalize'
        }}
      >
        {[{ id: 0, first_name: 'Tout' }, ...agents]?.map((option) => (
          <MenuItem
            key={option?.id}
            value={option?.id}
            sx={{
              mx: 1,
              my: 0.5,
              borderRadius: 0.75,
              typography: 'body2',
              textTransform: 'capitalize'
            }}
          >
            {option?.first_name} {option?.last_name}
          </MenuItem>
        ))}
      </TextField>

      <DatePicker
        label="Date de début"
        value={value}
        inputFormat="yyyy-MM-dd"
        onChange={(newValue) => {
          onChangeDate1(newValue);
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            sx={{
              maxWidth: { md: INPUT_WIDTH }
            }}
          />
        )}
      />

      <DatePicker
        label="Date de fin"
        value={value1}
        inputFormat="yyyy-MM-dd"
        onChange={(newValue) => {
          onChangeDate2(newValue);
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            sx={{
              maxWidth: { md: INPUT_WIDTH }
            }}
          />
        )}
      />
      <SearchStyle
        value={filterName}
        onChange={onFilterName}
        placeholder="Rechercher par ticket?..."
        startAdornment={
          <InputAdornment position="start">
            <Box component={Icon} icon={searchFill} sx={{ color: 'text.disabled' }} />
          </InputAdornment>
        }
      />

      <Tooltip title="Filter list">
        <LoadingButton loading={loading} variant="contained" onClick={handleGo}>
          Filtrer
        </LoadingButton>
      </Tooltip>
    </Stack>
  );
}
