import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import { paramCase } from 'change-case';
import { useRef, useState } from 'react';
import editFill from '@iconify/icons-eva/edit-fill';
import { Link as RouterLink } from 'react-router-dom';
import { LoadingButton } from '@mui/lab';
import { useSnackbar } from 'notistack';
// material
import { Menu, MenuItem, Button, ListItemIcon, ListItemText } from '@mui/material';
import Request from '../../../../utils/Request';
import { useDispatch, useSelector } from '../../../../redux/store';
import { getFiches } from '../../../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../../../routes/paths';
import { PERMISSIONS } from '../../../../guards/Permissions';
import useAuth from '../../../../hooks/useAuth';
import { handlePermission } from '../../../../utils/handlePermission';
// ----------------------------------------------------------------------

FIcheMoreMenu.propTys = {
  ficheId: PropTypes.number,
  elimine: PropTypes.bool
};

export default function FIcheMoreMenu({ ficheId, elimine }) {
  const ref = useRef(null);
  const { user } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();

  const handleBlock = async () => {
    if (!isOpen) {
      setIsOpen(true);
      const values = {
        ficheId
      };
      await Request.handleUpdateFiche(values);
      setIsOpen(false);
      dispatch(getFiches());
      enqueueSnackbar(!elimine ? 'Eliminé with success' : 'fiche valide with success', { variant: 'success' });
    }
  };

  return (
    <>
      {elimine
        ? handlePermission(user?.perms, PERMISSIONS.REACTIVATE_FICHE) && (
            <LoadingButton
              loading={isOpen}
              ref={ref}
              color={!elimine ? 'error' : 'primary'}
              size="small"
              variant="contained"
              onClick={() => {
                const confirmation = window.confirm(
                  `Êtes-vous sûr de bien vouloir ${!elimine ? 'Eliminé' : 'Reactiver'} ce fiche?`
                );

                if (confirmation) {
                  // Perform the deletion logic here
                  // You can call your API or update state as needed.
                  handleBlock();
                }
              }}
            >
              {!elimine ? 'Eliminé' : 'Reactiver'}
            </LoadingButton>
          )
        : handlePermission(user?.perms, 'delete_fichevente') && (
            <LoadingButton
              loading={isOpen}
              ref={ref}
              color={!elimine ? 'error' : 'primary'}
              size="small"
              variant="contained"
              onClick={() => {
                const confirmation = window.confirm(
                  `Êtes-vous sûr de bien vouloir ${!elimine ? 'Eliminé' : 'Reactiver'} ce fiche?`
                );

                if (confirmation) {
                  // Perform the deletion logic here
                  // You can call your API or update state as needed.
                  handleBlock();
                }
              }}
            >
              {!elimine ? 'Eliminé' : 'Reactiver'}
            </LoadingButton>
          )}
    </>
  );
}
