function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_fiche) => _fiche?.ref_code.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function Fiche() {
  const { themeStretch } = useSettings();
  const { user } = useAuth();
  const theme = useTheme();
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const { fiches, agents, surcussales, infotirages } = useSelector((state) => state.user);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('date');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(25);
  const [value, setValue] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const [value2, setValue2] = useState(0);
  const [value3, setValue3] = useState(0);
  const [value4, setValue4] = useState(0);
  const onChangeDate1 = (datavalue) => {
    setValue(datavalue);
  };
  const onChangeDate2 = (datavalue) => {
    setValue1(datavalue);
  };

  const handleGo = async () => {
    setLoading(true);
    await dispatch(getFiches(fDate(value), fDate(value1), value2, value3, value4));
    setLoading(false);
  };

  useEffect(() => {
    // handleGo();
    dispatch(getAgents());
    dispatch(getSurcussales());
    dispatch(getInfoTirages());
  }, [dispatch]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = fiches?.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - fiches.length) : 0;

  const filteredFiches = applySortFilter(fiches, getComparator(order, orderBy), filterName);

  const total = sum(filteredFiches?.map((item) => parseFloat(item.montant))).toFixed(2);

  const isFicheNotFound = filteredFiches?.length === 0;

  const groupedFiches = filteredFiches.reduce((acc, curr) => {
    if (!acc[curr.name]) {
      acc[curr.name] = [];
    }
    acc[curr.name].push(curr);
    return acc;
  }, {});

  // Map groupedFiches to array
  const ficheArray = Object.keys(groupedFiches).map((name) => ({
    name,
    fiches: groupedFiches[name]
  }));

  return (
    <Page title="Fiches vendus: List | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Liste des fiches vendus"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Fiches vendus', href: PATH_DASHBOARD.borlette?.list },
            { name: 'Listes' }
          ]}
        />

        <Card>
          <FicheListToolbar
            filterName={filterName}
            onFilterName={handleFilterByName}
            value={value}
            value1={value1}
            value2={value2}
            value3={value3}
            value4={value4}
            setValue2={setValue2}
            setValue3={setValue3}
            setValue4={setValue4}
            onChangeDate1={onChangeDate1}
            onChangeDate2={onChangeDate2}
            handleGo={handleGo}
            isFicheNotFound={isFicheNotFound}
            loading={loading}
            setLoading={setLoading}
            agents={agents}
            surcussales={surcussales}
            infoTirages={user?.role !== 'superuser' ? infotirages : TIRAGE}
            total={fCurrency(total)}
            count={fiches?.length}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              {ficheArray.map((group) => (
                <Stack spacing={3}>
                  <Typography variant="h5" sx={{ pl: 3 }}>
                    {group.name}
                  </Typography>
                  <Table>
                    <FicheListHead
                      order={order}
                      orderBy={orderBy}
                      headLabel={[
                        ...(user?.role === 'superuser'
                          ? [{ id: 'company', label: 'Compagnie', alignRight: false }]
                          : []),
                        ...TABLE_HEAD
                      ]}
                      rowCount={fiches?.length}
                      numSelected={selected?.length}
                      onRequestSort={handleRequestSort}
                      onSelectAllClick={handleSelectAllClick}
                    />
                    <TableBody>
                      {group.fiches.map((fiche) => (
                        <CollapsibleFicheRow key={fiche?.id} ite={fiche} role={user?.role} />
                      ))}
                    </TableBody>
                    <TableRow>
                      <TableCell colSpan={2} />
                      <TableCell align="center">
                        <Typography variant="subtitle2">Total</Typography>
                      </TableCell>
                      <TableCell align="left" width={100}>
                        <Typography variant="subtitle2">
                          {sum(group.fiches?.map((item) => parseFloat(item.montant))).toFixed(2)}
                        </Typography>
                      </TableCell>
                    </TableRow>
                  </Table>
                </Stack>
              ))}

              {!isFicheNotFound && (
                <TableRow>
                  <TableCell colSpan={4} />
                  <TableCell align="center">
                    <Typography variant="h6">Grand Total</Typography>
                  </TableCell>
                  <TableCell align="left" width={100}>
                    <Typography variant="h6">{fCurrency(total)}</Typography>
                  </TableCell>
                </TableRow>
              )}
              {isFicheNotFound && (
                <TableBody>
                  <TableRow>
                    <TableCell align="center" colSpan={10} sx={{ py: 3 }}>
                      <SearchNotFound searchQuery={filterName} />
                    </TableCell>
                  </TableRow>
                </TableBody>
              )}
            </TableContainer>
          </Scrollbar>
        </Card>
      </Container>
    </Page>
  );
}
